.footer {
  background: #f8f8f8;
  position: relative;
  height: 80px;
  bottom: 0;
  left: 0;
  right: 0;
  &_menu {
    display: flex;
    justify-content: space-around;
  }
  &_design {
    font-weight: bold;
    font-size: 16px;
    line-height: 150%;
  }
  &_nav {
    ul {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      li {
        margin: 0 40px;
      }
      a {
        text-decoration: none;
        font-weight: bold;
        font-size: 16px;
        line-height: 150%;
        color: #253757;
      }
    }
  }
}

@media (max-width: 1000px) {
  .footer {
    &_nav {
      ul {
        li {
          margin: 0 20px;
        }
      }
    }
  }
}

@media (max-width: 800px) {
  .footer {
    height: 250px;
    &_menu {
      flex-direction: column-reverse;
    }
    &_nav {
      ul {
        flex-direction: column;
        margin-bottom: 35px;
        a {
          font-size: 20px;
        }
      }
    }
  }
}
